import { Component } from '@angular/core';
import { ViewController,IonicPage,  NavController,Events , ActionSheetController, NavParams } from 'ionic-angular';
import {   FormBuilder,  FormGroup,   Validators } from '@angular/forms';
import { Auth,NotificationProvider } from '../../../providers';
import { TranslateService } from '@ngx-translate/core';
import { Storage } from '@ionic/storage';

@IonicPage()
@Component({
  selector: 'page-catalogo-selectable',
  templateUrl: 'catalogo-selectable.html',
})
export class CatalogoSelectablePage {

  titulox:any;
 idunidadt:any;
 idformulario:any;

  public form                  : FormGroup;
  username: string = "";
  public nombre                 : any;
frm:any;

  idempresa:any;
  private _ID                  : String;
  public pageTitle           	: string;

  constructor( public storage: Storage,public msj: NotificationProvider, public translateService: TranslateService,public viewCtrl: ViewController,public authService:  Auth,public navCtrl 		: NavController,
    public navParams 	: NavParams,   private _FB          : FormBuilder,         public actionSheetCtrl: ActionSheetController,
          public events: Events)
  {
    this.translateService.get('APPX').subscribe((value) => {
      this.idempresa=value
    });
     this.form = this._FB.group({
       nombre     : ['', Validators.required]



     });


  }

  ngOnInit() {
    this.storage.get('username').then((value) => {
      this.username = value;
  });
  }

  ionViewDidLoad() : void
  {
if(this.navParams.data.frm=='frmcats')
{
  this.idunidadt			=	this.navParams.data.op;
}
else{
  this.idunidadt			=	this.navParams.data.idunidad;
  this.idformulario=this.navParams.data.idformulario;
}

  this.frm=		this.navParams.data.frm;
    if(this.navParams.get("record")==null)
    {


      this.pageTitle='Creación de ' +this.idunidadt;
    }
    else {
       this._ID 				=	this.navParams.data.record._id;
       this.nombre		=	this.navParams.data.record.nombre;
       this.pageTitle='Actualización de ' +this.idunidadt;
    }
  }



  manageGallery() : void
  {
  let creat='Actualiza';
    if(this.navParams.get("record")==null)
    {  creat='Crea';
    }

    let   options       : any	 ;
    if(this.navParams.data.frm=='frmcats')
{
    options           = {
    idempresa: this.idempresa,
    idunidad : this.idunidadt,
   nombre : this.form.controls['nombre'].value,

   bitacora:{idempresa:this.idempresa,idafiliado:'',email: this.username,permiso:creat,accion:creat+' catalogos dinamicos '}
  };

}
else{

  options           = {
    idempresa: this.idempresa,
    idunidad : this.idunidadt,
    idformulario:this.idformulario,
   nombre : this.form.controls['nombre'].value,

   bitacora:{idempresa:this.idempresa,idafiliado:'',email: this.username,permiso:creat,accion:creat+' catalogos dinamicos '}
  };
}





     if(this.navParams.get("record")==null)
     {
          if(options){
            this.authService.createReg('',options,this.frm).then((result) => {
              if (!this.form.valid) { return; }
              this.viewCtrl.dismiss(result);
          }, (err) => {
            this.msj.showError3('Error',err.error);
          });
          }
     }
     else
     {
      if(options){
        this.authService.createReg(this._ID,options,this.frm).then((result) => {
           if (!this.form.valid) { return; }
        //   console.log(result)
           this.viewCtrl.dismiss(result);
       }, (err) => {
        this.msj.showError3('Error',err.error);
       });
    }}
  }

  cancel() {
    this.viewCtrl.dismiss();
  }
  done() {
    if (!this.form.valid) { return; }
    //this.viewCtrl.dismiss(this.form.value);
  }

}



